@import "normalize";
@import "rouge-github";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400&display=swap');


////////////
// COLORS //
////////////
$bg-white:     #fff;
$bg-gray:      #f6f8fa;
$border-gray:  #d0d7de;
$black:        #24292f;
$bright-black: #57606a;
$blue:         #0969da;
$bright-blue:  #218bff;


///////////////////
// MAGIC NUMBERS //
///////////////////
$col-width:  60px;
$col-sep:    20px;
$font-size:  16px;
$y-magic-no: 1.5 * $font-size; // y-magic-no is default line-height (1.5) x font-size


///////////////////
// GLOBAL STYLES //
///////////////////
* {
  box-sizing: border-box;
  margin:     0;
  padding:    0;
} 

html, body {
  height: 100%;
  width: 100%;
}

body {
  color:       $black;
  font-family: "Noto Sans", sans-serif;
  font-size:   $font-size;
  line-height: $y-magic-no;
}

#container {
  margin:   0 auto;
  overflow: auto; // expand the height of #container so that its includes its children
  padding:  2*$y-magic-no 1.5*$col-sep 0;
  position: relative;
  width:    1000px;
}

/////////////
// HEADERS //
/////////////
h1, h2, h3 {
  color:       $black;
  line-height: $y-magic-no;
  margin:      0 0 $y-magic-no;
}
h1 {
  font-size: 1.5*$font-size;
}
h2 {
  font-size: 1.25*$font-size;
}
h3 {
  font-size: 1.125*$font-size;
}
// h4 and above not used

////////////////////
// BLOCK ELEMENTS //
////////////////////
blockquote {
  background:    $bg-gray;
  border-left:   0.2*$col-sep solid $border-gray;
  border-right:  $col-sep solid $bg-gray;
  border-radius: 0 0.33*$y-magic-no 0.33*$y-magic-no 0;
  margin:        0 #{-$col-sep} $y-magic-no;
  padding:       0.5*$y-magic-no 0 0.5*$y-magic-no 0.8*$col-sep;
}
blockquote p {
  color: $black;
}
blockquote p:last-child {
  margin: 0;
}

figure {
  height: auto;
  margin: 0 auto $y-magic-no;
  max-width:  100%;
}
figurecaption {
  font-size: 0.75*$font-size;
  line-height: 1;
  margin:    0;
  max-width: 100%;
}

hr {
  border:     0;
  background: $bg-gray;
  height:     1px;
  margin:     #{$y-magic-no - 1px} 0 $y-magic-no;
}

iframe, img {
  max-width: 100%; // prevent iframe's and img's from overflowing width of parent figure
  height: auto;
}

p {
  margin: 0 0 $y-magic-no;
  text-align: justify;
}

///////////////////
// TEXT ELEMENTS //
///////////////////
a {
  color:           $blue;
  text-decoration: none;
}
a:hover, a:focus {
  color: $bright-blue;
}

h1 a, h2 a, h3 a, h4 a, strong a {
  color: $black;
}
h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, strong a:hover,
h1 a:focus, h2 a:focus, h3 a:focus, h4 a:focus, strong a:focus {
  color: $blue;
}

/////////////////////////////
// MONOSPACED/PREFORMATTED //
/////////////////////////////
code, pre {
  font-family: Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal, Consolas, Liberation Mono, DejaVu Sans Mono, Courier New, monospace;
  color:       $black;
}
pre {
  background:    $bg-gray;
  border:        1px solid $border-gray;
  border-radius: 0.25*$y-magic-no;
  color:         $black;
  font-size:     0.75 * $font-size;
  line-height:   $y-magic-no;
  margin:        0 0 $y-magic-no;
  overflow-x:    auto;
  padding:       #{0.5*$y-magic-no - 2px} $col-sep;
}


///////////
// LISTS //
///////////
dl, ol, ul {
  margin:     0 0 $y-magic-no $col-sep;
  text-align: justify;
}
li ul, li ol {
  margin: 0 0 $y-magic-no $col-sep;
}

dl {
  margin:      0 0 $y-magic-no;
}
dt {
  clear: left;
  color: $black;
  float: left;
  width: 2*$col-width;
}
dd {
  clear: right;
  margin: 0 0 0.5*$y-magic-no #{2*$col-width + $col-sep};
}
dd::after {  /* clears proceeding dt(s) ensuring that the next dd will be after next dt */
  content: "";
  clear:   left;
  display: table;
} 
dt p, dd p {
  text-align: left;
}


////////////
// TABLES //
////////////
table {
  border-top:      1px solid $border-gray;
  border-collapse: collapse;
  margin:          0 0 $y-magic-no;
  width:           100%;
}
thead {
  background: $bg-gray;
}
tr {
  border-bottom:  1px solid $border-gray;
  vertical-align: top;
}
th {
  padding:    #{0.5*$y-magic-no - 1px} 0.5*$y-magic-no;
  text-align: left;
} 
td {
  padding:    #{0.5*$y-magic-no - 1px} 0.5*$y-magic-no 0.5*$y-magic-no;
  text-align: left;
}


////////////
// HEADER //
////////////
header {
  float:    left;
  position: fixed;
  width:    3*$col-width + 2*$col-sep;
}

header a {
  color:           $black;
  text-decoration: none;
}
header a:hover, header a:focus {
  color: $blue;
}

header h1 {
  font-size:   1.75*$font-size;
  line-height: 1;
  margin-top:  #{-0.25*$font-size};
}

#profile-pic {
  margin: 0 0 $y-magic-no;
}

#profile-pic img {
  border:        1px solid $border-gray;
  border-radius: 50%;
  width:         7*$y-magic-no;
  padding:       5px;
}

nav ul {
  list-style-type: none;
  margin: 0 0 $y-magic-no;
}
nav ul li {
  font-size:   1.25*$font-size;
  line-height: $y-magic-no;
  margin:      0 0 $y-magic-no;
}
nav ul li a {
  font-weight: bold;
}

section {
  width:  9*$col-width + 8*$col-sep;
  float:  right;
  margin: 0;
}

footer {
  float:    left;
  position: fixed;
  bottom:   0;
  width:    3*$col-width + 2*$col-sep;
}


/////////////////////
// UTILITY CLASSES //
/////////////////////
.bullet-icon {
  margin-right: 0.25em;
  text-indent:  0;
}

.column {
  float:  left;
  margin: 0 20px 0 0;
  width:  50%;
}
.row::after {
  content: "";
  clear:   both;
  display: table;
}

.date {
  color:         $bright-black;
  margin-bottom: 0;
}

.no-bottom-margin {
  margin-bottom: 0;
}

small {
  color:       $bright-black;
  font-size:   0.75*$font-size;
  line-height: $y-magic-no;
}

///////////////////////////////////
// NARROW VIEWPORT MODIFICATIONS //
///////////////////////////////////
@media print, screen and (max-width: 960px) {
  body {
    width:     auto;
    min-width: 4*$col-width + 4*$col-sep;
  }
  #container {
    width:     auto;
    min-width: 4*$col-width + 3*$col-sep;
  }

  header, section, footer {
    float:    none;
    position: static;
    padding:  0;
  }
  
  section {
    border:       1px solid $border-gray;
    border-width: 1px 0;
    margin:       0;
    padding:      $y-magic-no 0 0;
    width:        auto;
    min-width:    4*$col-width + 3*$col-sep;
  }

  footer {
    margin:   $y-magic-no 0 0;
    overflow: auto;
  }
}
